import tokens from "../config/constants/tokens";
import {SupportedChainId} from "../config/constants/chains";



export default async function addAsset({ connector, tokenSymbol, chainId }: { connector: any
  tokenSymbol: string,

  chainId: SupportedChainId}){

  try {
    const provider = await connector.getProvider()
    await provider.request({
        method: 'wallet_watchAsset',
        params: {
          type: 'ERC20', // Initially only supports ERC20, but eventually more!
          options: {
            address: tokens[tokenSymbol].address[chainId], // The address that the token is at.
            symbol: tokenSymbol, // A ticker symbol or shorthand, up to 5 chars.
            decimals: tokens[tokenSymbol].decimals, // The number of decimals in the token
          },
        },
      });
  } catch (error) {
    //
  }
}
